var React = require('react');

var DisplayOutput = React.createClass({


  render: function() {

    var location = this.props.location;
    var temp = this.props.temp;

    var message = <p>It's {temp} in {location}</p>;

    return (
      <p>{message}</p>
    );
  }
});

module.exports = DisplayOutput;



